.accordion__icon {
  width: 22px;
  height: 13px;
  transition: transform var(--animation-500ms);
}
.accordion__content {
  --sides-padding: 76px;
  position: relative;
  overflow: hidden;
  padding: 0 var(--sides-padding);
  max-height: 0;
  box-sizing: content-box;
  font: var(--font-md);
  line-height: 34px;
  color: var(--color-small-text);
  background-color: var(--color-white);
  opacity: 0;
  transition: all var(--animation-500ms);
  will-change: max-height;
}
.accordion__content::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  display: block;
  margin: 0 auto;
  width: calc(100% - var(--sides-padding) * 2);
  height: 1px;
  background: rgba(209, 206, 199, 0.5);
}
@media screen and (max-width: 860px) {
  .accordion__content {
    --sides-padding: 50px;
  }
}

.open .accordion__icon {
  transform: rotate(180deg);
}

.open .accordion__content {
  padding: 20px var(--sides-padding) 30px;
  opacity: 1;
}

